.right-panel {
  height: calc(100vh - 83px);
  width: calc(100% - 400px);
  padding: 30px 30px 20px 75px; }
  .right-panel .right-panel_content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start; }
    .right-panel .right-panel_content .panel-content-header {
      font-family: starjedi;
      font-size: 36px;
      line-height: 40px;
      color: #ffe81f;
      margin-bottom: 40px;
      display: flex;
      align-items: center; }
      .right-panel .right-panel_content .panel-content-header button {
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
        padding: 7px 20px;
        color: #000000;
        background: #ffe81f;
        font-size: 18px;
        font-weight: 600;
        border-radius: 50px;
        display: flex;
        align-items: center;
        border: 0;
        outline: none;
        margin-left: 50px; }
        .right-panel .right-panel_content .panel-content-header button svg {
          fill: #000000;
          margin-right: 5px; }
        .right-panel .right-panel_content .panel-content-header button:hover {
          border: 2px solid #ffe81f;
          color: #ffe81f;
          background: transparent;
          cursor: pointer; }
          .right-panel .right-panel_content .panel-content-header button:hover svg {
            fill: #ffe81f; }
    .right-panel .right-panel_content .panel-content-data {
      font-size: 14px;
      line-height: 20px;
      color: #bababa;
      margin: 10px 0; }
      .right-panel .right-panel_content .panel-content-data span {
        font-size: 16px;
        line-height: 16px;
        color: #ffe81f;
        margin-right: 20px;
        margin-bottom: 10px;
        text-decoration: underline; }
